import React from 'react';
import AnchorLink from 'react-anchor-link-smooth-scroll';
import LogoIcon from '../../svg/LogoIcon';
import { StaticImage } from "gatsby-plugin-image"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSearch, faBars, faTimes } from '@fortawesome/free-solid-svg-icons'

class Header extends React.Component {

  constructor(props) {
    super(props);

    this.state = { navOpen: false }

    this.toggleNav = this.toggleNav.bind(this);
  }

  toggleNav() {
    console.log('toggle nav');


    this.setState(prevState => ({
      navOpen: !prevState.navOpen
    }));

  }

  render() {
    return (
      <header className="sticky top-0 z-50 h-20 bg-colorHeader">
        <div className={`${this.state.navOpen ? "block" : "hidden"} navigation-mobile absolute h-screen mt-20 pt-20 pl-5 w-full lg:hidden text-xl font-bold bg-color2t`}>
          <AnchorLink offset='100' href="#ziel" onClick={this.toggleNav}>
            Ziel
          </AnchorLink>
          <a href="https://erp.digital-cluster-uri.ch/events" target="_blank">Events</a>
          <AnchorLink offset='100' href="#nutzen" onClick={this.toggleNav}>
            Nutzen
          </AnchorLink>
          <AnchorLink offset='100' href="#mitgliedschaft" onClick={this.toggleNav}>
            Mitgliedschaft
          </AnchorLink>
          <AnchorLink offset='100' href="#umfrage" onClick={this.toggleNav}>
            Umfrage
          </AnchorLink>
          <AnchorLink offset='100' href="#news" onClick={this.toggleNav}>
            News(letter)
          </AnchorLink>
          <AnchorLink offset='100' href="#traeger" onClick={this.toggleNav}>
            Projektträger
          </AnchorLink>
        <AnchorLink offset='100' href="#kontakt" className=" text-color3 pt-10" onClick={this.toggleNav}>
          Kontakt
        </AnchorLink>
          </div>
        <div className="flex justify-between items-center mx-5 pt-6">
          <div className="">
            <div className="flex items-center">
              <img className="h-8 w-auto mr-4" src="/icon-dc.svg" alt="Digital Cluster Uri - Icon"/>
              <img className="h-5 w-auto" src="/logo-dc.svg" alt="Digital Cluster Uri - Logo"/>
            </div>
          </div>
          {/* Desktop nav */}
          <div className="hidden lg:block">
            <div className="navigation text-sm font-bold">
              <AnchorLink offset='100' href="#ziel">
                Ziel
              </AnchorLink>
              <a href="https://erp.digital-cluster-uri.ch/events" target="_blank">Events</a>
              <AnchorLink offset='100' href="#nutzen">
                Nutzen
              </AnchorLink>
              <AnchorLink offset='100' href="#mitgliedschaft">
                Mitgliedschaft
              </AnchorLink>
              <AnchorLink offset='100' href="#umfrage">
                Umfrage
              </AnchorLink>
              <AnchorLink offset='100' href="#news">
                News(letter)
              </AnchorLink>
            </div>
          </div>
          <div className="hidden lg:block">
            <AnchorLink offset='100' href="#kontakt" className="text-sm font-bold text-color3 hover:text-color1 transition duration-300">
                Kontakt
            </AnchorLink>
          </div>
          {/* Mobile nav */}
          {/* <div className={`lg:hidden text-sm font-bold ${this.state.navOpen ? "block" : "hidden"}`}> */}
          <div className="lg:hidden text-2xl cursor-pointer text-color3 hover:text-color1 transition duration-300" onClick={this.toggleNav}>
            <FontAwesomeIcon icon={ this.state.navOpen ? faTimes : faBars } />
          </div>
        </div>

        
        {/* <div className="">
          <div className="">
            <StaticImage
              className="h-full"
              src="../../images/logo.png"
              alt="Logo"
            />

          </div>
        </div>
        <div className={`navigation ${this.state.navOpen ? "show-nav" : ""}`}>
          <AnchorLink href="#ziel">
            Unser Ziel
          </AnchorLink>
          <AnchorLink href="#nutzen">
            Dein Nutzen
          </AnchorLink>
          <AnchorLink href="#mitglieder-partner">
            Mitglieder & Partner
          </AnchorLink>
          <AnchorLink href="#news">
            News(letter)
          </AnchorLink>
        </div>
        <div className="right-navigation">
          <AnchorLink href="#kontakt">
              Kontakt
          </AnchorLink>
        </div>
        <div className="menu-button" onClick={this.toggleNav}>
          <FontAwesomeIcon icon={ this.state.navOpen ? faTimes : faBars } />
        </div> */}
      </header>
    )
  }
};

export default Header;
